.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  /* background-color: #59788E; */
  background-color:#24282e;
  font-family: 'Dancing Script', cursive;
  font-family: 'Dosis', sans-serif;
  font-family: 'Raleway', sans-serif;
}


/* Regsitration page css */

.FormBox{
  position: absolute;
  left:25%;
  top:14%;
  width:40%;
  background-color: azure;
  padding: 3%;
  min-height: 30%;
  border-radius: 30px;
  height: fit-content;
  padding-bottom: 1%;
  transition: 0.5s ease;
}
.FormBox:hover{
  transition: 0.5s;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 54px 55px, rgba(0, 0, 0, 0.3) 0px -12px 30px, rgba(0, 0, 0, 0.28) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.FormBox form{
  display: flex;
  flex-direction: column;
}
.FormBox form input{
  margin:3%;
  padding: 1%;
  width: 75%;
  align-self: center;
  height: 20%;
  background-color: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 110%;
}
.FormBox form label{
  margin:3%;
  padding: 1%;
  width: 75%;
  align-self: center;
  height: 20%;
  background-color: transparent;
  border-top: none;
  border-left: none;
  border-right: none;
  font-size: 110%;
  display: flex;
  font-size: 150%;
  cursor: pointer;
}
.FormBox form p{
  align-self: center;
}

.FormBox form input:focus{
  outline: none;
}
.FormBox form #S{
  width:35%;
  padding: 2.5%;
  background-color: #282c34;
  color: aliceblue;
  border-radius: 25px;
  font-weight: 450;
  transition: 0.3s ease;
}
.FormBox form #S:hover{
  background-color: #000000;
  transition: 0.3s;
}

/* profile page css */

.profileBox{
  position:absolute;
  background-color: aliceblue;
  padding:2%;
  display: flex;
  width:50%;
  left:25%;
  min-height:20%;
  top:30%;
  border-radius: 25px;
  transition: 0.5s ease;
  height: fit-content;
}
.profileBox:hover{
  transition: 0.5s;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 54px 55px, rgba(0, 0, 0, 0.3) 0px -12px 30px, rgba(0, 0, 0, 0.28) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.profileBox .Info{
  position:absolute;
  left:34%;
  top:20%;
  font-size: 140%;
  white-space:pre-wrap;
  height: fit-content;
}
.profileBox button{
  position: absolute;
  left:72%;
  top:52%;
  width: 14%;
  height: 20%;
  background-color: rgba(255, 0, 0, 0.676);
  border: none;
  border-radius: 20px;
  font-size: 120%;
  cursor: pointer;
  color: aliceblue;
  font-weight: 700;
}
.profileBox button:hover{
  background-color: red;
}
.profileBox img{
  width:23%;
  height: 87%;
  cursor:pointer;
  border-radius:30px;
  position: absolute;
  left: 4%;
  top:2%;
  border-radius: 50%;
  top:6%;
}

/* Heading css */
.home .heading{
  font-size: 100px;
  position: absolute;
  top:-5%;
  color: azure;
  left:5%;
}

@media only screen and (max-width:720px){
  .FormBox{
    left:15%;
    top:14%;
    width:70%;
    padding: 2%;
  }
  .FormBox form input{
    margin:7%;
    padding: 2%;
  }
  .FormBox form label{
    margin:3%;
    padding: 1%;

  }
  .FormBox form #S{
    width:55%;
    padding: 4%;
    background-color: #282c34;
    color: aliceblue;
    border-radius: 25px;
  }
  .profileBox{
    top:50%;
    width: 80%;
    left: 15%;
    right: 15%;
    display:table;
    flex-direction: row;
    min-height: 60%;
  }
  .profileBox .Info{
    left:15%;
    top:30%;
  }
  .profileBox button{
    left:15%;
    top:76%;
    height: 13%;
    width: fit-content;
    padding: 2%;
    padding-left: 5%;
    padding-right: 5%;
  }
  .profileBox img{
    width:150px;
    height: 100px;
  }
}
@media only screen and (min-width:720px) and (max-width:850px) {
  .profileBox{
    top:50%;
    width: 70%;
    left: 15%;
  }
}

@media only screen and (max-height:600px) {
  .profileBox{
    top:30%;
    height: 60%;
    width: 85%;
    left:5%;
  }
  .home .heading{
    font-size: 50px;
    position: absolute;
    top:-5%;
  }
}